
<template>
  <div v-if="toy" class="toy-details">
    <section>
      <h1>welcome to Toy details page</h1>
      <h3>{{ toy._id }}</h3>
      <h3>{{ toy.name }}</h3>
      <h3>{{ toy.createdAt }}</h3>
      <h3>{{ toy.inStock }}</h3>
      <h3>{{ toy.price }}</h3>
      <h3>{{ toy.type }}</h3>
      <router-link to="/toy">Back</router-link>
    </section>

    <section>
      <h3>Add review:</h3>
      <form @submit.prevent="addReview">
        <label for="txt">review:</label>
        <input type="text" v-model="review.txt" />
        <button>Add</button>
      </form>

      <ul class="review-list">
        <li v-for="review in toyToShow.review" :key="review._id">
          {{ review.txt }}
          <!-- <div class="btns-container">
            <button @click="remove(toy._id)">X</button>
            <router-link :to="'/details/' + toy._id">Details</router-link>
            <router-link :to="'/edit/' + toy._id">Edit</router-link>
          </div> -->
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
import { toyService } from "../services/toy.service.js";

export default {
  name: "toy-details",
  data() {
    return {
      toy: null,
      review: {
        txt: "",
      },
    };
  },
  computed: {
    toyToShow() {
      return this.toy;
    },
  },
  methods: {
    loadToy() {
      const id = this.$route.params.toyId;
      toyService.getById(id).then((toy) => (this.toy = toy));
    },
    async addReview() {
      await toyService.addReview(this.toy._id, this.review);
      await this.$store.dispatch({ type: "loadToys" });
      await this.loadToy;
    },
  },
  watch: {
    "$route.params.toyId"(id) {
      console.log("Changed to", id);
      this.loadToy();
    },
  },
  created() {
    this.loadToy();
  },
};
</script>

