

<template>
  <div class="toy-app">
    <section class="flex-center">
      <h1>Toy app page</h1>
    </section>

    <toy-filter @filtered="setFiltered"></toy-filter>
    <button @click="moveToAddToy">Add Toy</button>
    <toy-list :toys="toysForDisplay" @remove="removeToy"></toy-list>
  </div>
</template>




<script>
// @ is an alias to /src
import toyFilter from "@/cmps/toy-filter.vue";
import toyList from "@/cmps/toy-list.vue";

export default {
  name: "Toy-app",
  methods: {
    moveToAddToy() {
      this.$router.push("/toy-add");
    },
    removeToy(toyId) {
      this.$store
        .dispatch({ type: "removeToy", toyId })
        .then(() => {
          // showMsg("toy remove successfully");
          this.$store.dispatch({ type: "loadToys" });
        })
        .catch((err) => {
          // showMsg("error with remove toy", "danger");
        });
    },
    setFiltered(filter) {
      this.$store.commit({
        type: "setFilter",
        filterBy: JSON.parse(JSON.stringify(filter)),
      });

      this.$store.dispatch({
        type: "setFilter",
        filterBy: JSON.parse(JSON.stringify(filter)),
      });
    },
  },
  computed: {
    toysForDisplay() {
      return this.$store.getters.toysForDisplay;
    },
  },
  created() {
    console.log("Toy app created!!!");
    this.$store.dispatch({ type: "loadToys" });
  },
  components: {
    toyFilter,
    toyList,
  },
};
</script>

